	//import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
	export default {

		data() {
			return {
				editType: 'edit',
				ishow:0,   //地址框石佛显示  1显示
				citynum:0,
				style1:'1',
				style2:'0',
				style3:'0',
				style4:"0",
				isdisabled:false, //判断地址石佛填完
				city_one:[],  //省地址
				city_two:[],  //市地址
				city_tree:[],  //区地址
				city_four:[],  //街道地址
				id: '',
				username: '',          //用户名
				tel: '',         //手机号
				detailed: '',   //详细地址
				address:"",
				province: "请选择",
				city: "",
				area: "",
				street: "请选择",
				isDefault: 0,  //设置为默认地址 1
				strId:"",
				themeColor: '#007AFF',
				region:null,  //编辑地址对象
				visible: true,
				
			}
		},
		methods: {
			//调取地址弹出框
			selectAddr(){
				this.ishow=1;
				this.citynum=0;
				this.init(0,1)
				
			},
			showcity(id,name){
				// this.pid=id
				this.init(id,2)
				this.citynum=1
			    this.province=name
				// this.id1=id
				 this.style1=id
			     this.city_tree=[]
				 this.ciry_four=[]
				 this.isdisabled = false;
				 this.street="请选择"	
			},
			showcitytwo(id,name){
				//this.pid=id
				console.log(name)
				this.init(id,3)
				this.citynum=2
				this.city=name
				//this.id2=id
				this.style2=id
				this.isdisabled = false;
			},
			showcitythree(id,name){
				//this.pid=id
				 //this.init(id)
				this.area=name;
				//this.id3=id
				this.style3=id
				this.isdisabled = true;
				this.strId=id;
				this.citynum=4
			},
			showcityfour(id,name){
				
				this.street=name;
				//this.id3=id
				this.style4=id
				this.isdisabled = true;
				
				
			},
			//获取街道
			chooseStr(){
				
				this.ishow=3;
				this.init(this.strId,4)
				
			},
			// 取消
			cancel(){
				this.ishow=0
			},
			//确认
			sure(){
				if(this.isdisabled==true){
					
					this.ishow=0
					this.address=this.province+"-"+this.city+"-"+this.area;
				}else{
					uni.showToast({
						title:"请选择区域",
						icon:'none'
					})
					
				}
			},
			
			init(pid,level){
				// 获取
				this.request({
					url: this.config.url+'area',
					data: {
						pid:pid,
						token:this.token,
						level:level
					},
					method: 'POST',
					success: (res) => {
						console.log(res)
							
						if(this.citynum==0){
							this.city_one=res.data.data
						}else if(this.citynum==1){
							this.city_two=res.data.data
						}else if(this.citynum==2){
							this.city_tree=res.data.data
						}else if(this.citynum==4){
							
						this.city_four=res.data.data;
							
						}
					},
				})
			},
			
			//设置默认地址
			isDefaultChange(e){
				console.log(e.detail.value)
				if(e.detail.value){
					this.isDefault=1;
				}else{
					this.isDefault=0;
				}
			},
                //  新建/修改地址提交
						save() {
							console.log(this.tel, this.detailed)
							let data = {
								"username": this.username,
								"phone": this.tel,
								id: this.id,
								token: this.token,
								province: this.province,
								city: this.city,
								area: this.area,
								street: this.street,
								"is_default": this.isDefault,
								address: this.detailed
							}
			
							if (!this.username) {
								uni.showToast({
									title: '请输入收件人姓名',
									icon: 'none'
								});
								return;
							}
							if (!this.tel) {
								uni.showToast({
									title: '请输入收件人电话号码',
									icon: 'none'
								});
								return;
							}
							if (!this.detailed) {
								uni.showToast({
									title: '请输入收件人详细地址',
									icon: 'none'
								});
								return;
							}
			
							// uni.showLoading({
							// 	title:'正在提交'
							// })
							//实际应用中请提交ajax,模板定时器模拟提交效果
							this.request({
								url: this.config.url + "address/edit",
								method: "post",
								data: data,
								success: (res) => {
									console.log(res)
									if (res.data.code == 1) {
										uni.showToast({
											title:res.data.info,
											duration:1000,
										})
										uni.navigateBack({
											
										})
									}else if(res.data.code==0){
										uni.showToast({
											title:res.data.info,
											duration:1000,
										})
									}
								}
							})
			
			
						}
		},
		onLoad(option) {
			//this.getSiteData()
			//获取传递过来的参数
			 if(option.type){
				  this.region=JSON.parse(option.type)
			 }
			
			 console.log(option,this.region)
			if (this.region) {
			
				this.name = this.region.name;
				this.tel = this.region.phone;
				this.detailed = this.region.address;
				this.id = this.region.id;
				this.province = this.region.province;
				this.city = this.region.city;
				this.area = this.region.area;
				this.street = this.region.street;
				this.isDefault=this.region.is_default;
			}


		},
		mounted(){
			//加载默认地址
			// this.address=this.province+"-"+this.city+"-"+this.area;
			this.address="请选择"
			this.street=this.street;
		}

	}
